@import "vars";

body {
    font-family: Arial, sans-serif;
    font-size: 22px;
    color: $text-color;
}

.wrap {
    margin: 0 auto;
    
    padding: 4px;
}

:fullscreen a {
    display: flex
}

h1 {
    font-size: 2em;
    color: $pink;
    margin: 0 0 1em 0;
}

h2 {
    font-size: 1.5em;
    margin: 0 0 1em 0;
    color: $blue;
}